}

.fb {
  background-color: #3B5998;
}

.tw {
  background-color: #55ACEE;
}

.google {
  background-color: #DD4B39;
}

.footer__platform-box {
  display: flex;
}

.footer__platform-box__text {
  display: flex;
  flex-direction: column;
