.menu-box {
    display: flex;
    margin-bottom: 160px;
}
.menu-box__title {
    margin-bottom: 19px;
    margin-top: 80px;
    font-size: 35px;
}
.menu-box__element-info {
    display: flex;
    margin-bottom: 10px;
    flex-direction: column;
}
.menu-box__elements {
    width: 531px;
    height: 297px;
}
.menu-box__element {
    display: flex;
    justify-content: space-between;
    border-bottom: 1px solid rgba(#BABABA, .5);
    margin-top: 10px;
}

.menu-box__element:last-of-type {
    border-bottom: none;
}

.menu-box__element-title {
    font-weight: 600;
    line-height: 17px;
}

.menu-box__element-price {
    padding: 0 40px;
    font-weight: 300;
    font-size: 26px
}

.menu-box__rigth {
    width: 572px;
    margin-left: 38px;
}

.menu-box__elements{
    position: relative;
}

.menu-box__special-img--front {
    background-image: url("../assets/special/pizza.jpg");
    width: 320px;
    height: 210px;
}

.menu-box__special {
    width: 320px;
    -webkit-box-shadow: -5px 59px 155px -56px rgba(0,0,0,0.75);
-moz-box-shadow: -5px 59px 155px -56px rgba(0,0,0,0.75);
box-shadow: -5px 59px 155px -56px rgba(0,0,0,0.75);
}

.menu-box__special-desc {
    padding: 30px;
    padding-left: 40px;
    height: 150px;
    width: 100%;
    z-index: 999;
    background-color: #fff;
    
}
.menu-box__special-desc:nth-of-type(1){
    z-index: -1;
} 


.menu-box__special-img--back {
    width: 320px;
    height: 172px;
    background-image: url("../assets/special/chicken.jpg")
}

.menu-box__special:nth-last-of-type(1){
    position: absolute;
    top: 8%;
    left: 50%;
    z-index: -1;
}

.menu-box__special-desc__title {
    font-size: 22px;
    font-weight: 500;
}

.menu-box__special-desc__price {
    font-size: 22px;
    color: $color-primary;
}

.btn-menu {
    color: #fff;
    width: 199px;
    height: 51px;
    margin-left: 10px;
}